var page_title_display;
var locate = false;
function hashChangeHandler(e) {
	var hash = document.location.hash.replace("#","");
	var page_title;
	
	console.log("hash: "+hash);
	switch (hash) {
		case "helper":
			if (!$("body").hasClass("helper")) {
				page_title = $("header.controls .page_title span").html();
				page_title_display = $("header.controls .page_title").css("display");
			}
			locate = $("body").hasClass("locate");
			
			if (!$("section.helper").hasClass("active")) {
				console.log("come on");
				$(".back_nav").addClass("inactive");
				$(".controls .profile").removeClass("inactive");
				$("body").addClass("helper").removeClass("profile");
				$("header.controls .page_title span").html("Concierge");
				$("header.controls .page_title").css("display","block");
				$(this).addClass("signpost");
				$("div.search_station").addClass("inactive");
				$("header.controls").addClass("toolactive");
				$("section.helper").removeClass("choices profile");
				$(".choice_info").html("");
				$(".helper .navigate").removeClass("active");
				$(window).scrollTop(0);
				setTimeout(function() {
					try {
						webkit.messageHandlers.callbackHandler.postMessage("white");
					} catch (e) {

					}
				},600);
			}
			break;
		case "journey_alerts":
			$("header .price").addClass("inactive");
			$("body").addClass("helper").removeClass("profile");
			$(this).addClass("signpost");
			$(".helper .navigate").addClass("active");
			$("section.helper").addClass("choices");
			$(".back_nav").removeClass("inactive");
			break;
		case "profile":
			$(".controls .profile").addClass("inactive");
			$(".back_nav").removeClass("inactive");
			$("header .price").addClass("inactive");
			$("section.helper").removeClass("choices");
			$("body").addClass("helper").addClass("profile");
			$(this).addClass("signpost");
			$("div.search_station").addClass("inactive");
			page_title = $("header.controls .page_title span").html();
			setTimeout(function() {
				 $("header.controls .page_title span").html("Profile");
				$(".choice_info").html('<span class="name">Good morning<br><strong>John</strong></span><small>Log out</small>');
				try {
					webkit.messageHandlers.callbackHandler.postMessage("white");
				} catch (e) {

				}
			},500);
			break;
		case "locating":
			// enterLocating();
			break;
		default:
			openDestinationScreen();
			$(".controls .profile").addClass("inactive");
			if (!$("body").hasClass("search_station")) {
				$(".back_nav").removeClass("inactive");
			}
			$("footer").removeClass("active");
			$(".route_vertical").removeClass("active");
			$("header.controls").removeClass("toolactive navigateactive");
			try {
				webkit.messageHandlers.callbackHandler.postMessage("white");
			} catch (er) {
				console.log("oh no");
			}
			if ($("section.helper").hasClass("choices") || $("section.helper").hasClass("profile")) {
				$("section.helper").removeClass("choices profile");
				console.log("closing slowly");
				document.location.hash="";
				setTimeout(function() {
					$("body").removeClass("helper");
					$("header .price").removeClass("inactive");
					$("a.burger").removeClass("signpost");
					$("section.helper").removeClass("active")
					$("div.search_station").removeClass("inactive");
					$("header.controls .page_title span").html(page_title);
					$("header.controls .page_title").css("display",page_title_display);
				},450);
				$("body.home .back_nav").addClass("inactive");
				break;
			} else {
				document.location.hash="";
				console.log("go back!");
				$(".controls .profile").addClass("inactive");
				$("body.home .back_nav").addClass("inactive");
				$("header.controls .page_title span").html(page_title);
				$("header.controls .page_title").css("display",page_title_display);
				$("body").removeClass("helper");
				$("header .price").removeClass("inactive");
				$(this).removeClass("signpost");
				
				$("section.helper").removeClass("active")
				$("div.search_station").removeClass("inactive");
				if (locate) {
					$("body").addClass("locate");
					$(".route_vertical").addClass("active");
					$("div.search_station").addClass("inactive");
				}
			}
			$(".choice_info").html("");
			break;
	}

}
$(function() {
	$("section.helper header .navigate a, section.helper .delete_message").bind("click",function(e) {
		$(window).unbind('hashchange');
		document.location.hash="helper";

		$("section.helper").removeClass("choices profile");
		$(".choice_info").html("");
		$("header.controls").removeClass("navigateactive").addClass("toolactive");
		e.preventDefault();
		$(window).bind('hashchange', hashChangeHandler);
	});
	$("section.helper, section.helper .swt_avatar p").bind("click", helperToggle);
})
function helperToggle(e) {
	if (!$("body").hasClass("helper")) {
		$("a.burger").trigger("click");
	}
}