var headerMinHeight = null;
var historyCount=0;
$(function() {
	headerMinHeight=parseInt($(".headerMinHeight").css("height"));
	try {
		webkit.messageHandlers.callbackHandler.postMessage("white");
	} catch (er) {}
	function stepHandler() {
		if ($("body").hasClass("step1")) {
			// for (i=0;i<history.length;i++) {;
			document.location.href='planning.php';
		}
		if ($("body").hasClass("step2")) {
			$("body").removeClass("step1 step2 step3 step4");
			$("body").addClass("step1");
			trainDirection = "outbound";
			setSlideMinNow();
			updatePriceList();
			$(".outbound li, .returning li, .ticket_options li").removeClass("faded");
			$(".schedule_container .opacityZero").css("opacity",0);
			schedule.sizeUp();
		}
		if ($("body").hasClass("step3")) {
			$("body").removeClass("step1 step2 step3 step4");
			$("body").addClass("step2");
			updatePriceList();
			trainDirection = "returning";
			$(".schedule_container .opacityZero").css("opacity",0);
			$(".outbound li, .returning li, .ticket_options li").removeClass("faded");
			schedule.sizeUp();
		}
		if ($("body").hasClass("step4") && !$("body").hasClass("confirmation-page")) {
			$("body").removeClass("step1 step2 step3 step4");;
			$("body").addClass("step3");
			$(".schedule_container .opacityZero").css("opacity",0);
			$(".ticket_options li").removeClass("faded");
			schedule.sizeUp();
		}
		if ($("body").hasClass("confirmation-page")) {
			document.location.href='index.php#scanner';
		}
	}
	function scrollHandler(e) {
		var scrollTop = $(window).scrollTop();
		var scrollPoint = 5;
		if (scrollTop>scrollPoint && !$("body").hasClass("scrolled")) {
			$("body").addClass("scrolled");
		} else {
			if (scrollTop<=scrollPoint) {
				$("body").removeClass("scrolled");
			}
		}
	}
	$(".back_nav").bind("click",function() {
		if ($("body").hasClass("helper")) {
			document.location.hash='helper';
		} else if ($("body").hasClass("schedule")) {
			stepHandler();
		} else if ($("body").hasClass("planning")) {
			document.location.href='index.php#scanner';
		} else {
			history.go(-1);
		}
	});
	$(window).bind("scroll",scrollHandler);
	$("a.filters").bind("click",function(e) {
		$(".swt_modal").addClass("active");
		$("body").addClass("modals");
		$(".swt_modal > div").removeClass("active");
		$(".back_nav").addClass("inactive");
		$(".swt_modal .departure_controls").addClass("active");
		$(".swt_modal").removeClass("trainmessage");
		$("a.burger").addClass("close-button");
		e.preventDefault();
		return false;
	});
	$("a.burger").bind("click",function(e) {
		if ($("body").hasClass("schedule")) {
			historyCount++;
		}
		$(window).unbind('hashchange');
		$("header.controls").removeClass("toolactive navigateactive");
		if ($(this).hasClass("close-button")) {
			$("body").removeClass("station_picker");
			if ($(".swt_modal").hasClass("active")) {
				$(".swt_modal").removeClass("active train_message");
				$("body").removeClass("modals");
				$(".back_nav").removeClass("inactive");
			} else {
				closeJourneyScreen();
				$("body").removeClass("helper");
				setTimeout(enterHomeScreen,550);
					setTimeout(function() {
					setTimeout(function() {
						$(".home .top").css("transition-duration","0.4s").css("transition-timing-function","cubic-bezier(0,.91,.12,.99)");
						$(".home .bottom").css("transition-duration","0.4s").css("transition-timing-function","cubic-bezier(0,.91,.12,.99)");
					},100);
					setTimeout(function() {
						$(".home .top").css("transform","translateY(0)").css("opacity",1);
						$(".home .bottom").css("transform","translateY(0)").css("opacity",1);
					},110);
				},200);
			}
			$(this).removeClass("close-button");
		} else if ($(this).hasClass("signpost")) {
			if ($("section.helper").hasClass("choices") || $("section.helper").hasClass("profile")) {
				document.location.hash="";
				setTimeout(function() {
					$("body").removeClass("helper");
					$("header .price").removeClass("inactive");
					$("a.burger").removeClass("signpost");
					$("section.helper").removeClass("active")
					$("div.search_station").removeClass("inactive");
				},450);
			} else {
				document.location.hash="";
				$("body").removeClass("helper");
				$("header .price").removeClass("inactive");
				$(this).removeClass("signpost");
				$("section.helper").removeClass("active")
				$("div.search_station").removeClass("inactive");
			}
			$(".choice_info").html("");
		} else {
			
			document.location.hash="helper";
			hashChangeHandler();
			$("body").addClass("helper");
			$(this).addClass("signpost");
			$("section.helper").unbind();
		}
		$(window).bind('hashchange', hashChangeHandler);
		e.preventDefault();
		return false;
	});
	$(".choices-menu button.journey_alerts").bind("click",function() {
		historyCount++;
		$(window).unbind('hashchange');
		document.location.hash="journey_alerts";
		$("header.controls").removeClass("toolactive").addClass("navigateactive");
		$("section.helper").addClass("choices");
		$(window).bind('hashchange', hashChangeHandler);
	});
	
	$(".controls .profile").bind("click",function(e) {
		if ($(".touch_test").css("display")=="inline") {
			e.preventDefault();
			document.location.href="profile.php";
		} else {
			e.preventDefault();
			document.location.hash="profile";
		}
		
	});
});